@import url(https://fonts.googleapis.com/css?family=Lato:300,400,900&display=swap);
@import url(https://fonts.googleapis.com/css?family=Lato:300,400,900&display=swap);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

h1,h2,h3,h4,h5,h6{margin-bottom:.5rem;font-weight:500;line-height:1.2}h1{font-size:2.5rem}h2{font-size:2rem}h3{font-size:1.75rem}h4{font-size:1.5rem}h5{font-size:1.25rem}h6{font-size:1rem}html{box-sizing:border-box}*,*::before,*::after{box-sizing:inherit}body{font:normal normal 300 16px "Lato",sans-serif}.container{margin:1rem}

h1,h2,h3,h4,h5,h6{margin-bottom:.5rem;font-weight:500;line-height:1.2}h1{font-size:2.5rem}h2{font-size:2rem}h3{font-size:1.75rem}h4{font-size:1.5rem}h5{font-size:1.25rem}h6{font-size:1rem}.profile{position:relative;background-color:#fff;margin-top:-194px;height:400px;width:100%;border:none;box-shadow:2px 2px 5px 2px rgba(0,0,0,0.16),2px 2px 10px 2px rgba(0,0,0,0.12)}.profile__image{position:absolute;left:0;right:0;bottom:85%;width:150px;margin-left:auto;margin-right:auto}.profile__image img{width:150px;height:150px;border-radius:50%;box-shadow:2px 2px 5px 2px rgba(0,0,0,0.16),2px 2px 10px 2px rgba(0,0,0,0.12)}.profile__container{padding:10px}.container__infos{max-width:100%;margin-top:50px;text-align:center}.container__infos--name{position:relative;display:block;color:#6c757d;font-size:2.18rem;padding-top:10px}.container__infos--job{font-size:1.56em;color:#6c757d;text-transform:capitalize;margin-top:10px}.profile__contact{max-width:100%;font-size:1rem;margin-left:50px}.profile__contact .contact__mail,.profile__contact .contact__skype,.profile__contact .contact__phone,.profile__contact .contact__place{margin-top:10px}.contact__mail span,.contact__skype span,.contact__phone span,.contact__place span{margin-left:10px}.profile__social-media{margin-top:60px;text-align:center}.social-media__icon{display:inline;cursor:pointer}.social-media__icon:not(:first-of-type){margin-left:10px}@media (min-width: 789px){.profile{height:400px}}

.toolbar{position:relative;display:block;width:100%;height:370px;background:url(/my-pages/static/media/toolbar_background.4dc07cdf.jpg) no-repeat;background-position:center center;background-size:cover;background-attachment:fixed}

